.FormField {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #2ecc71;
  padding: 14px 10px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
}
.FormField:focus {
  width: 280px;
  border-color: #3498db;
}
.img {
  width: 100px;
  height: 100px;
  margin: auto;
  margin-bottom: 2rem;
}
